.navbar{
    background: #1A120B;
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content:space-between;
    position: sticky;
    top: 0;
    z-index: 3;

}


.logo{
    object-fit: cover;
    height: 2.5rem;
    width: 5rem;
}

.desktopListItem{
    margin: 3rem;
    cursor: pointer;

}

.desktopListItem:hover{
    color:#b1ee59;
    padding-bottom: 0.3rem;
    border-bottom: 2px solid #b1ee59;
}

.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
    padding: 0.05rem;
    cursor: pointer;

}


.desktopMenubtn{
    background:whitesmoke;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    font-weight: bold;
    cursor: pointer;
}

.active{
    color:#b1ee59 ;
    border-bottom: 3px solid #b1ee59;
    padding-bottom: 0.5rem;
}

.menu{
    display: none;
    object-fit: cover;
    height: 1.8rem;

}

.navMenu{
    position:absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width:15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.ListItem{
    color:whitesmoke;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30,30);
}

@media screen and (max-width:720px) {

    .menu{
        display:flex;

    }

    .desktopMenu{
        display: none;

      }

    .desktopMenubtn{
        display: none;
    }


    
}